@import url('https://fonts.googleapis.com/css2?family=Archivo+Black&family=Kosugi+Maru&family=Train+One&display=swap');

.App {
	text-align: center;
	width: 100%;
	height: 100vh;
	font-family: Kosugi Maru;
	color: rgb(120, 104, 88);
}

@media screen and (min-width: 481px) {
	.pokemonCardContainer {
		display: grid;
		grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
		place-items: center;
		gap: 25px;
		margin: 20px 20px;
	}
}

@media screen and (max-width: 480px) {
	.pokemonCardContainer {
		display: grid;
		grid-template-columns: 1fr;
		place-items: center;
		gap: 20px;
		margin: 5px 5px;
	}
}